/* analysis.module.css */

/* 移除容器限制 */
.analysisContainer {
    background: transparent;
    margin: 0;
    padding: 0;
  }
  
  .previewSection {
    position: relative;
    max-width: 800px;
    margin: 2rem auto;
  }
  
  .previewContent {
    position: relative;
    line-height: 1.6; /* 需要固定行高 */
    max-height: calc(3 * 1.6em); /* 显示三行示例 */
    overflow: hidden;
  }
  
  .blurOverlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: calc(2 * 4.6em); /* 模糊最后两行 */
    background: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0) 0%,
      rgba(237, 235, 235, 0.9) 100%
    );
    backdrop-filter: blur(4px);
    mask-image: linear-gradient(
      to bottom,
      transparent 0%,
      black 30%
    );
  }
  .unlockBtn {
    display: block;
    margin: 1rem auto 0;
    padding: 8px 16px;
    background: #000;
    color: white;
    border: none;
    cursor: pointer;
    position: relative;
    z-index: 1; /* 确保按钮在模糊层上方 */
  }
  
  .fullAnalysis {
    margin-top: 2rem;
  }
  