/* src/App.module.css */
/* 全局样式 */
body {
  font-family: 'Segoe UI', system-ui, sans-serif;
  line-height: 1.6;
  background-color: #f5f5f7;
  color: #333;
  margin: 0;
  padding: 20px;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
/* Header 样式优化 */
.header {
  padding: 1.5rem;
  border-radius: 12px;
  margin-bottom: 0.5rem; /* 32px */
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.branding {
  text-align: left;
}

.branding h1 {
  margin: 0;
  font-size: 1.7rem;
  color: #2c3e50;
}

.slogan {
  margin: 0.5rem 0 0;
  color: #7f8c8d;
  font-size: 1.1rem;
  font-style: italic;
}

.logo {
  max-width: 160px;
  height: auto;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: rotate(-5deg);
}

/* 响应式设计：小屏幕设备 */
@media (max-width: 768px) {
  .header-content {
    justify-content: space-between;
  }

  .logo {
    max-width: 120px; /* 在小屏幕下缩小 logo */
  }
}

/* 主要内容区域 */
.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* 错误提示 */
.error-message {
  padding: 1rem;
  margin: 1rem 0;
  background: #ffe3e3;
  color: #c92a2a;
  border-radius: 8px;
  border: 1px solid #ffc9c9;
  animation: shake 0.4s ease;
}

/* 加载状态 */
.loading {
  padding: 2rem;
  text-align: center;
  font-size: 1.2rem;
  color: #4a5568;
  position: relative;
}

.loading::after {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  border: 2px solid #ddd;
  border-radius: 50%;
  border-top-color: #4a5568;
  animation: spin 1s linear infinite;
  margin-left: 0.5em;
}

/* 底部 */
.sfooterContent {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.sdisclaimer {
  font-size: 0.85rem;
  color: #6c757d;
  line-height: 1.5;
  margin: 0;
}

@media (max-width: 768px) {
  .footer {
    padding: 1rem 15px;
  }
  
  .disclaimer {
    font-size: 0.75rem;
  }
}

/* 支付部分 */
/* PaymentModal.module.css */
.payment-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(4px);
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 16px;
  padding: 2rem;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  animation: modalEnter 0.3s ease-out;
}

@keyframes modalEnter {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #2d3748;
}

.close-button {
  background: none;
  border: none;
  font-size: 1.5rem;
  line-height: 1;
  padding: 0 0.5rem;
  cursor: pointer;
  color: #718096;
  transition: color 0.2s;
}

.close-button:hover {
  color: #2d3748;
}

.qrcode-wrapper {
  text-align: center;
  padding: 1rem 0;
  position: relative;
}

.countdown {
  margin-top: 1rem;
  color: #e53e3e;
  font-weight: 500;
  font-size: 0.9rem;
}


.modal-footer {
  margin-top: 1.5rem;
  text-align: center;
}

.modal-footer p {
  color: #718096;
  font-size: 0.9rem;
  margin: 0;
}

/* 响应式设计 */
@media (max-width: 480px) {
  .modal-content {
    padding: 1.5rem;
    border-radius: 12px;
  }

  .modal-header h3 {
    font-size: 1.1rem;
  }

  .qrcode-wrapper {
    padding: 0.5rem 0;
  }
}

/* 二维码样式增强 */
.react-qr-code {
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 8px;
  background: white;
}
